.imghvr-effect .imghvr-img:after,.imghvr-effect .imghvr-img:before {
  position: absolute;
  content: '';
  z-index: 1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: 0;
  -webkit-transition: all .5s ease-in-out;
  -khtml-transition: all .5s ease-in-out;
  -moz-transition: all .5s ease-in-out;
  -ms-transition: all .5s ease-in-out;
  -o-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out;
}

.imghvr-effect .imghvr-img:before {
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0;
}

.imghvr-effect:hover .imghvr-img:before {
  opacity: 1;
  height: 100%;
  width: 100%;
  background-color: rgba(0,0,0,.6);
}

.imghvr-effect .mask {
  top: 50%;
  left: 50%;
  position: relative;
  -webkit-transform: translate(-50%,-50%);
  -khtml-transform: translate(-50%,-50%);
  -moz-transform: translate(-50%,-50%);
  -ms-transform: translate(-50%,-50%);
  -o-transform: translate(-50%,-50%);
  transform: translate(-50%,-50%);
  text-align: center;
  position: absolute;
  z-index: 11;
}

.imghvr-effect .mask span {
  padding: 0 0;
  margin: 0 5px;
  font-size: 18px;
  background-color: #285bff;
  color: #fff;
  width: 36px;
  height: 36px;
  line-height: 36px;
  display: inline-block;
  opacity: 0;
  -webkit-transition: all .5s ease-in-out;
  -khtml-transition: all .5s ease-in-out;
  -moz-transition: all .5s ease-in-out;
  -ms-transition: all .5s ease-in-out;
  -o-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out;
  -webkit-transform: scale(0);
  -khtml-transform: scale(0);
  -moz-transform: scale(0);
  -ms-transform: scale(0);
  -o-transform: scale(0);
  transform: scale(0);
}

.imghvr-effect .mask span .fa_link {
  background: url(../images/arrow_link.svg) no-repeat center center;
  display: inline-block;
  width: 36px;
  height: 36px;
}

.imghvr-effect:hover .mask span {
  opacity: 1;
  -webkit-transform: scale(1);
  -khtml-transform: scale(1);
  -moz-transform: scale(1);
  -ms-transform: scale(1);
  -o-transform: scale(1);
  transform: scale(1);
}

.owl-theme .owl-nav {
  position: absolute;
  width: 100%;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
  -khtml-transform: translate(-50%,-50%);
  -moz-transform: translate(-50%,-50%);
  -ms-transform: translate(-50%,-50%);
  -o-transform: translate(-50%,-50%);
  transform: translate(-50%,-50%);
}

.owl-theme .owl-nav .owl-prev {
  position: absolute;
  left: -30px;
}

.owl-theme .owl-nav .owl-next {
  position: absolute;
  right: -30px;
}

